:root {
  --clr-primary: #f07777;
}

.profiledropdown {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  background-color: var(--clr-primary);
  padding: 0.125em 0.25em;
}

.profile:focus-within .profiledropdown,
.profile:hover .profiledropdown {
  display: block;
}

.profiledropdown p {
  margin: 0.25em;
}
