.navbar {
  background-color: #777;
  display: grid;
  grid-template-columns: 1fr 1fr 5fr 4fr 1fr;
  align-items: center;
}

.navlist {
  display: flex;
  list-style: none;
  grid-column: -3;
  gap: 2em;
  justify-content: space-around;
  align-items: center;
  margin: 0;
}

.navimg {
  grid-column: 2;
  cursor: pointer;
}

.selected {
  color: gold;
}
